	module.exports = {"data":{"markdownRemark":{"html":"<p>冬に向けて絶賛修羅場中な訳だが無理してたせいか、睡魔が臨界点を突破して来たので15分ほど仮眠のつもりでソファーに横になったら、思いっきり3時間くらい眠ってたみたい。まぁそれはさておき、その時に見た夢がまた凄かったんだ。 ──現在より数ヶ月ほど未来の話の夢。PCの画面内で初音ミクが泣いていた。今までTBSやGoogle、はたまた愛情表現の過ぎた一部の作家さんたちにいじめられ、彼女の唯一の安らぎの場、ニコニコにも裏切られるなど苦難の一途を辿っていた。そして今、ミクの歌声はどこにも聞こえない。彼女の歌声に似た雑音だけはあちこちから聞こえるのに。彼女と主の間に生まれた歌声はたちまちカネやら権利やらのノイズにまみれ、あの時に似ても似つかぬ雑音と化してしまう。いつしか二人は歌うのをやめていた。みんなもやめていた。ミクは泣いていた。主は慰めるすべもなくただ彼女を見つめていた。 ある日、ぼくは鏡音リン・レンを買った。CDドライブに放り込むとPCの画面にミクと鏡音姉弟の3人の人影が映った。ミクは「私の分も(鏡音姉弟を)幸せにしてあげて」と言い残し、次の瞬間には彼女の瞳から輝きは失せ、体は動かなくなっていた。 ぼくは涙と叫びたい気持ちを必死に抑え、鏡音レンのパッケージと嘆願書を片手に オ リ エ ン ト 工 業 本 社 へと向かっていた── と、ここで目が覚めたんだが、最後の一文が己の深層心理なのだと、今 の 僕 に は 理 解 出 来 な い</p>","frontmatter":{"cover":null,"date":"2007-12-24T01:26:00+09:00","strDate":"2007/12/23","title":"夢って己の深層心理を現しているんだっけ"}}},"pathContext":{"path":"/ja/blog/2007/12/24-夢って己の深層心理を現しているんだっけ/","langKey":"ja"}}

